/* Visita Text Animation */


.visita-text-animation h1 {
  max-width: 40ch;
  text-align: center;
  transform: scale(0.94);
  animation: scale 3s forwards cubic-bezier(0.5, 1, 0.89, 1);
}
@keyframes scale {
  100% {
    transform: scale(1);
  }
}

.visita-text-animation span {
  display: inline-block;
  opacity: 0;
  filter: blur(4px);
  margin: 0 7px 0 0;
}

.visita-text-animation span:nth-child(1) {
  animation: fade-in 01s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(2) {
  animation: fade-in 01s 0.2s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(3) {
  animation: fade-in 01s 0.3s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(4) {
  animation: fade-in 01s 0.4s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(5) {
  animation: fade-in 01s 0.5s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(6) {
  animation: fade-in 01s 1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(7) {
  animation: fade-in 01s 0.7s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(8) {
  animation: fade-in 01s 01s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(9) {
  animation: fade-in 01s 0.9s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(10) {
  animation: fade-in 01s 1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

 .visita-text-animation span:nth-child(11) {
  animation: fade-in 01s 1.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

 .visita-text-animation span:nth-child(12) {
  animation: fade-in 01s 1.2s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

 .visita-text-animation span:nth-child(13) {
  animation: fade-in 01s 1.3s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

 .visita-text-animation span:nth-child(14) {
  animation: fade-in 01s 1.4s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(15) {
  animation: fade-in 01s 1.5s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(16) {
  animation: fade-in 01s 1.6s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(17) {
  animation: fade-in 01s 1.7s forwards cubic-bezier(0.11, 0, 0.5, 0);
}

.visita-text-animation span:nth-child(18) {
  animation: fade-in 01s 11s forwards cubic-bezier(0.11, 0, 0.5, 0);
}



